.Wrapper{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    width: 100%;
    height: 100%;
    min-height: 100vh;
}
.Wrapper-Inner {
    max-width: 60%;
    width: 100%;

}
.Page-Wrapper{
    margin-top: 15vh;
}
/* navigation */
.Navigation{
    position: fixed;
    top: 0;
    backdrop-filter: blur(10px);
    width: 100%;
    padding: 15px 0;
}
 .Navigation__Links-First{
    margin-right: 3vw;
    display: flex;
    align-items: center;
}
 .Navigation__Links-First .chakra-link{
    display: flex;
    align-items: center;
}
 .Navigation__Links {
    margin: 0 1vw;
    display: flex;
    align-items: center;
}
.chakra-ui-dark  .Navigation__Links.active button{
    background-color: rgba(255,255,255,0.3);
}
.chakra-ui-light .Navigation__Links.active button{
    background-color: #E2E8F0;
}

.Navigation__Links button{
    padding: 7px 10px;
}
.Navigation-Desk{
    display: flex;
}
.Navigation-Mobile {
    display: none;
    margin-left: 3vw;
}


/* intro */
.Introduction{
    display: flex;
    /* margin-top: 5vh; */
    /* justify-content: space-between; */
    width: 100%;
}
.Introduction__Text{
    margin-left: 1vw;
}
.Introduction__Occupation{
   margin-bottom: 1vh;
}
.Introduction__Description{
    margin-left: 2vw;
}
.Introduction__Description > li{
    margin: 1.5px 0;
}
.Tech-Icon {
    margin: 0 3px;
}
.Summary {
    margin-top: 5vh;
}
.Summary__Text{
    margin-top: 1vh;
}
.Download-Btns{
    display: flex;
    justify-content: space-between;
    margin: 5vh 0 2vh;
}
.Skills{
    margin-top: 5vh;
}
.Skills__List{
    margin: 15px 0;
}
.Skills__List li{
    margin: 3px 0;
}
.ListItem-VK .chakra-code,
.ListItem-VK{
    display: flex;
    align-items: center;
}
.ListItem-VK .Icon--logo_vk_16 svg, 
.ListItem-VK .Icon--logo_vk_outline_24 svg {
    margin: 0 2px;
}
.Skills__List .chakra-code,
.Skills__Environiment .chakra-kbd{
    margin: 0 3px;
}
.Skills__Environiment .chakra-kbd svg{
    margin-inline-end: unset;
}
.Skills__Environiment{
    margin-top: 5vh;
}
.Skills__Environiment li{
    display: flex;
}

/* experience */
.Experience-Wrapper{
    margin-top: 5vh;
}
.Experience__Inner{
    display: flex;
    align-items: center;
}
.Experience__Description{
    margin-left: 2vw;
}
.Experience__Text{
    margin-top: 2vh;
}
.Experience__Description span{
    margin: 2px 0;
}

/* contact */
.Contact-List{
    text-align: left;
}
.Contact-List li{
    margin: 5px;
}

@media(max-width:960px){
    .Wrapper-Inner{
        max-width: 80%;
    }
    
}
@media(max-width: 660px) {
    .Wrapper-Inner{
        max-width: 90%;
    }
    .Navigation{
        padding: 15px 2vw 0;
    }
    .Navigation-Mobile {
        display: block;
    }
    .Navigation-Desk{
        display: none;
    }
    .Navigation__Links-First .chakra-heading{
        display: none;
    }
    .Introduction{
        flex-direction: column;
        align-items: center;
    }
    .Introduction .chakra-heading,
    .Introduction .chakra-text,
    .Introduction__Text,
    .Summary__Heading,
    .Summary__Text{
        text-align: center;
    }
    .Introduction__Description .chakra-code{
        width: 100%;
    }
    .Download-Btns{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 5vh;
    }
    .Download-Btns button{
        margin: 10px 0;
    }
    .Skills__Environiment li{
       display: block;
    }
    .Page-Wrapper .chakra-heading{
        text-align: center;
    }
    .Experience__Description{
        margin-top: 2vh;
    }
    .Experience__Inner{
        display: flex;
        flex-direction: column;
    }
    
}